.quiz-container {
  display: flex;
  background-color: #6FBACB;
  color: #FFFFFF;
  font-weight: 500;
  position: relative;
}

.quiz {
  display: flex;
  height: 100vh;
  width: 100vw;
}

.question-container {
  width: 80%;
  padding-top: 15vh;
}

.quiz small {
  font-weight: 400;
  font-size: 20px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  padding-bottom: 2rem;
}

.quiz h1 {
  font-weight: 600;
  font-size: 36px;
  text-align: center;
  letter-spacing: 0.05em;
  padding-bottom: 5rem;
  padding-top: 1rem;
}

.quiz span {
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  letter-spacing: 0.05em;
}

.quiz .answer {
  font-weight: 500;
  font-size: 24px;
}

.quiz .description {
  font-weight: 400;
  font-size: 18px;
}

.arrow {
  color: #E8F7F0;
  margin: auto;
  width: 10%;
}

.arrow:hover {
  color: #3DB9B1 !important;
  cursor: pointer;
  transform: translate(0, 5px);
}
    
.disabled-arrow {
  color: #FF9E74;
  margin: auto;
  width: 10%;
  visibility: hidden;
}

.disabled-arrow:hover{
  color: #FF9E74;
}

.back {
  float: left;
  margin-left: 1rem;
}

.next {
  float: right;
  margin-right: 1rem;
}

input[type=range] {
  -webkit-appearance: none;
  margin: 10px 0;
  width: 100%;
  background-color: #6FBACB;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 20px;
  cursor: pointer;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  background: #C8F1F1;
  border-radius: 25px;
  border: 0px solid #000101;
  overflow: hidden;
}
input[type=range]::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 30px;
  width: 8vw;
  border-radius: 20px;
  background: #FF7D45;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -3.6px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #C8F1F1;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 12.8px;
  cursor: pointer;
  animation-duration: 0.2s;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  background: #C8F1F1;
  border-radius: 25px;
  border: 0px solid #000101;
  overflow: hidden;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 20px;
  width: 39px;
  border-radius: 7px;
  background: #65001c;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 12.8px;
  cursor: pointer;
  animation-duration: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 39px 0;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #ac51b5;
  border: 0px solid #000101;
  border-radius: 50px;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
}
input[type=range]::-ms-fill-upper {
  background: #ac51b5;
  border: 0px solid #000101;
  border-radius: 50px;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
}
input[type=range]::-ms-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 20px;
  width: 39px;
  border-radius: 7px;
  background: #65001c;
  cursor: pointer;
}
input[type=range]:focus::-ms-fill-lower {
  background: #ac51b5;
}
input[type=range]:focus::-ms-fill-upper {
  background: #ac51b5;
}

.mask-woman {
  position: absolute;
  bottom: 0;
  left: 0;
  float: left;
  padding-left: 10%;
}

.mask-woman .img {
  width: 20vw;
  height: auto;
}

.mask-man {
  position: absolute;
  bottom: 0;
  right: 0;
  float: right;
  padding-right: 10%;
}

.mask-man .img {
  width: 20vw;
  height: auto;
}
